import lbt from '@/components/jing-swiper/jing-swiper.vue';
export default {
	components:{
		lbt
	},
	data() {
		return {
			tixian: 0,
			tixian1: '',
			shopid: ''
		}
	},
	onLoad:function(option) {
		this.tixian = option.money
		this.shopid = option.shopid
	},
	methods: {
		ktixian:function(){
			this.tixian1 = this.tixian
		},
		liji:function(){
			var that = this
			if(this.tixian1==0&&this.tixian1==''){
				uni.showToast({
					title: '提现金额不能为空',
					icon:'none'
				});
			}else{
				uni.showLoading({
				    title: '加载中'
				});
				uni.request({
					url: this.$bashUrl+ '/index.php/home/Api/store_cash/',
					method: 'POST',
					data:{
						id: this.shopid,
						cash: this.tixian1
					},
					success:(res=>{
						uni.hideLoading();
						console.log(res.data)
						if(res.data == 1){
							uni.showToast({
								title: '提现成功',
								icon:'none'
							});
							// uni.navigateTo({
							// 	url: '/pages/BusinessSide/tixianList/tixianList?shopid=' + this.shopid
							// })
							this.tixian = this.tixian - this.tixian1
							this.tixian1 = ''
						}else{
							uni.hideLoading();
							uni.showToast({
								title: '余额不足',
								icon:'none'
							})
						}
					})
				})	
			}
		}
	}
}
